.App-container {
	width: 70%;
	margin-left: 15%;
	margin-right: 15%;
	padding: 1%;
	/*margin-bottom: 20px;*/
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	overflow: auto;
}

.Plate {
	position: relative;
	width: 24%;
	padding-top: 20%;
	margin: 1% 0;
	text-align: center;
	background-color: #f9ba32;
	border-radius: 15px;
	-webkit-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
	        box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
	cursor: pointer;
}

.Plate-hover {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: none;
	z-index: 1;
	background-color: rgb(0, 0, 0);
	border-radius: 15px;
}

.Plate:hover .Plate-hover {
	display: block;
}

.Image {
	z-index: 1;
	border-radius: 15px;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

@media only screen and (max-width: 1000px) {
	.App-container {
		width: 96%;
		margin: 0;
		padding: 2%;
	}
	.Plate-hover {
		display: none;
	}

	.Plate:hover .Plate-hover {
		display: none;
	}
}



